@import '../../sass/variables';

.main_blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: $color-footer;

    &-image {
        width: 100%;
        height: 100%;
        background: $color-bg;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    
        padding: 1rem 1.5rem;
        height: 100%;

        h3 {
            font-family: $font-family;
            font-size: 25.11px;
            font-weight: 800;
            line-height: 30.30px;
            
            color: $color-white;
            margin-bottom: 5rem;
            cursor: pointer;
        }

        p {
            font-family: $font-family;
            font-size: 11.65px;
            font-weight: 700;
            line-height: 35px;
        
            color: $color-white;
        }

        p:last-child {
            cursor: pointer;
        }
        
    }
    
}


@media screen and (max-width: 550px) {
    .main_blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}